* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  justify-content: center;
  gap: 30px;
  margin-top: 20px;
  display: flex;
}

.field {
  width: 80px;
  height: 80px;
  text-align: center;
  background-color: rgba(255, 255, 255, .17);
  border: 2px solid rgba(0, 0, 0, .32);
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

button {
  width: 60px;
  height: 60px;
  cursor: pointer;
  color: #1b1919;
  border: none;
  border-radius: 35px;
  font-size: 12px;
  display: inline-block;
  box-shadow: inset 0 -23px 25px rgba(255, 255, 255, .17), inset 0 -36px 30px rgba(0, 0, 0, .15), inset 0 -79px 40px rgba(177, 156, 156, .1), 0 2px 1px rgba(101, 95, 95, .06), 0 4px 2px rgba(0, 0, 0, .09), 0 8px 4px rgba(0, 0, 0, .09), 0 16px 8px rgba(0, 0, 0, .09), 0 32px 16px rgba(0, 0, 0, .09);
}

